<template>
	<el-dialog title="视频" v-model="dialogTableVisible">
	  <video-player style="font-size: 5px;padding-top: 20px;height: 360px;" :options="videoOptions" ></video-player>
	</el-dialog>
</template>
<script>
import { videoPlayer } from 'vue-video-player';
export default{
  data:function(){
    return {
    	dialogTableVisible:false,
    	videoOptions: {}
    }
  },
  components: {
       videoPlayer,
  },
  created:function(){
  	  const that = this;
      const Bus = global.eventHub;
        Bus.$on('doto_show_jqshiping',function(msg){
          console.log(msg);
            that.dialogTableVisible=true;
        		that.videoOptions={
    				    source: {
    				      type: "video/mp4",
    				      src:msg,
                  // "http://www.laikabase.de/videos/Shift.mp4"
    				    },
    				    muted: false,
    				    controls:true,
    				    live: true,
    				    autoplay: false,
    				    height: 300
    				}
        });
    }
}

</script>



